body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-container{
    display: flex;
    flex-direction: column;
}
.home-container-main {
    text-align: center;
    font-size: 1rem;
    background-color: #CE2829;
    color:hsl(38, 44%, 96%);
    width: 98.5vw;
    min-height: 58vw;
    background-image: url("/mvp-banner.png");
    background-position: bottom center;
    background-size: contain;
    background-repeat: no-repeat;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.home-container-main .text span{
    font-family: "Satisfy";
    font-size: 1.5rem;
    color:#FDC913;
}
.second-header>span{
    font-family: "Satisfy";
    font-size: 1.5rem;
    color:#CE2829;
}
.home-container-main .text{
    font-family: "Barlow";
    font-size: 3rem;
}
.nav-bar, .second-nav-bar{
    font-size: 1rem;
    color:#FAF7F2;
    display: flex;
    justify-content: space-evenly;
    padding: 1rem 0;
    font-weight: bold;
}
.nav-bar>a:hover{
    color:#CE2829;
}
.nav-bar>a, .second-nav-bar>a{
    text-decoration: none;
    color: #292929;
    background-color: #fff;
    width: 10rem;
    border-radius: 30px;
}
.second-nav-bar>a:hover{
    color:#FAF7F2;
    background-color: #292929;
    text-align: center;
    cursor: pointer;
    outline: none;
    border: none;
    border-radius: 30px;
}
.home-container-second{
    background-color: #FAF7F2;
    padding-bottom: 5rem;
}
.first-banners{
    width: 80%;
    height: 50vh;
    display:flex;
    justify-content: space-evenly;
    margin: 0 auto;
    padding: 5rem 0 5rem 0;
}
.banner{
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    padding: 3rem 0 3rem 2rem;
    justify-content: space-evenly;
    align-items: stretch;
}
.banner h5{
    width:9rem;
}
.banner a{
    border-radius: 30px;
    text-decoration: none;
    color:#CE2829;
    width:7rem;
    background-color: #FAF7F2;
    text-align: center;
}
.half-banner1 h2, .half-banner1 h5{
    height: .1rem;
}
.half-banner1, .half-banner2{
    display: flex;
}
.half-banner2{
    flex-direction: column;
    justify-content: flex-end;
    align-content: stretch;
}
.c1{
    background-color: #CE2829;
    background-image: url("/esnek/kart-1.png");
    background-size: cover;
    width: 35vw;
    color:#FAF7F2;
}
.c2{
    background-color:#292929;
    color:#FAF7F2;
    background-image: url("/esnek/kart-2.png");
    background-size: cover;
    width: 35vw;
    height: 19vh;
    margin: 0 0 1rem 0;
}
.c3{
    background-image: url("/esnek/kart-3.png");
    background-size: cover;
    width: 35vw;
    height: 19vh;
    margin: 1rem 0 0 0;
}
.second-header {
    margin: 2rem 0 1rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.redd{
    color:#CE2829;
}
.food-cards{
    display: flex;
    justify-content: space-evenly;
}
.card1, .card2, .card3{
    background-color:#fff;
    width: 25vw;
    margin-top: 1.5rem;
    display: flex;
    flex-direction:column;
    border-radius: 7px;
    padding: 0 1rem;
}
.card1>img, .card2>img, .card3>img{
    align-self: center;
}
.card-details{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-size: .8rem;
}
footer{
    display: flex;
    flex-direction: row;
    background-color: #1d1c1c;
    color:#FAF7F2;
    padding: 2rem 7rem;
    justify-content: space-between;
}
.menu-info{
    display: flex;
    justify-content: space-between;
    width: 40%;
}
.insta-info{
    display:flex;
    width: 25%;
    flex-direction: column;
}
.insta-img{
    flex-wrap: wrap;
    justify-content:center ;
}
.insta-img img{
    padding: .5rem;
}
.footer-menus h4, .insta-info h4{
    color:#FAF7F2;
    padding-bottom: 1.5rem;
}
.contact-info{
    width:31%;
}
h3, h4 {
    color: #292929;
}
.food-info{
    color: #292929;
    top: 25rem;
}
.food-info h3{
    margin-bottom: 2.5rem;
}
.info-details b {
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
}
.part1, .part2, .part3{
    color:#5F5F5F;
}
.acikgri{
    color:#5F5F5F;
}
.total{
    color: #CE2829;
}
.food-content{
    margin: 0 auto;
    width: 40%;
}
.preferences {
    display:flex;
    flex-direction:row;
    justify-content: flex-start;
}
.part1{
    margin-right: 15rem;
}
.preferences, .part3, .note, .summary{
    padding: 2.5rem 0;
}
.info-details{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.comments-rate{
    display: flex;
    flex-direction: row;
    width: 10rem;
    margin-top:.5rem;
    justify-content: space-between;
}
.secim-summary, .total-summary{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
 }
 .total-price{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 0 0 5rem;
    width: 65%;
 }
 .total-summary h6{
    color:#CE2829;
    padding-bottom: 1rem;
 }
 .secim-summary h6{
    padding-top: 2rem;
    color:#292929;
 }
 .summary {
    display: flex;
    flex-direction: row;
 }
.counter {
    width: 30%;
}
.part3{
    display: flex;
    flex-direction: column;
}
.additionals{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-top:1rem;
    justify-content: space-between;
}
.form-check-inline{ /* reactstrapten gelen input-label class */
    flex-grow: 1;
    flex-shrink: 0;
    flex-basis: 30%;
    margin-bottom: 1rem !important;
}
.main-container{
    display:inline-block;
    flex-direction: column;
    align-items: center;
}
.order-form{
    align-items: center;
    text-align:left;
}
.nav-menu {
    display:flex;
    justify-content: center;
 }
 .nav-menu > a{
    text-decoration: none;
    color: #5F5F5F;
    padding: 2.5rem 2rem;
 }
 .nav-menu > a:last-child{
   text-decoration: none;
   color: #CE2829;
   padding: 2.5rem 2rem;
}
 .nav-menu > a:hover{
    text-decoration: none;
    color: #CE2829;
 }
 .beige-rectangle{
    display: flex;
    flex-direction: column;
    background-color: #FAF7F2;
    align-content: center;
    height: 30vh;
    position: relative;
    width:99vw;
 }
 .head-img{
    margin: 0 auto;
 }
 .head{
   height: 5vh;
   padding: 0 auto;
   font-family: "Londrina Solid";
   text-align: center;
   background-color: #CE2829;
   color:#FAF7F2;
   margin-bottom: 0;
   width:100%;
 }
 

.main-cont{
    background-color: #CE2829;
    height: 100vh;
}
.texts span{
    font-family: "Satisfy";
    font-size: 1.5rem;
    color: #FDC913;
}
.texts{
    font-family: "Barlow";
    font-size: 4rem;
    margin: 0 auto;
    text-align: center;
    color:#FAF7F2;
}
.head h2{
    padding-top: 2rem;
}
hr{
    color:#FAF7F2;
}
.order-content, .total-summarys, .secim-summarys{
    color:#FAF7F2;
}
.order-detailss{
    display:flex;
    flex-direction:column;
    align-items: center;
    margin:0 auto;
    height: 69vh;
    padding-top: 5rem;
}
.order-content h4{
    text-align: center;
    margin: 2rem auto 3rem auto;
    display:block;
}
.secim-summarys, .total-summarys{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 1rem;
}
.total-prices{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top:7rem;
    width: 30%;
}
.total-summarys{
    padding-bottom: 1rem;
}
.head{
    height: 5vh;
    padding: 0 auto;
    font-family: "Londrina Solid";
    text-align: center;
    background-color: #CE2829;
    color:#FAF7F2;
    margin-bottom: 0;
  }
  .order-content{
    width: 30%;
  }

header{
    background-color: #CE2829;
    text-align: center;
    height: 15vh;
    color:white;
}
h1{
    padding-top: 5%;
    font-family:  "Londrina Solid";
}
.main-content{
    width:100%;
}
